import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { withRouter } from "react-router";
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import DieselOrGas from './dieselOrGas';
import Manufacturer from './manufacturer';
import Floorplan from './floorplan';
import Model from './model';
import american_coach_logo from '../img/american_coach/american_coach_logo.png'
import dynamax_logo from '../img/dynamax/dynamax.png'
import entegra_logo from '../img/entegra/EntegraCoach.png'
import fleetwood_logo from '../img/fleetwood/fleetwood.png'
import monaco_logo from '../img/monaco/monaco.png'
import newmar_logo from '../img/newmar/Newmar.png'
import tiffin_logo from '../img/tiffin/tiffin.png'
import winnebago_logo from '../img/winnebago/winnebago.gif'
import logo from '../img/white-logo.svg'
import american_dream_2019 from '../img/american_coach/2019 american dream.png'
import american_dream_42B from '../img/american_coach/american dream 42B.png'
import american_dream_42Q from '../img/american_coach/american dream 42Q.png'
import american_dream_42S from '../img/american_coach/american dream 42S.png'
import american_dream_45A from '../img/american_coach/american dream 45A.png'
import american_eagle_2019 from '../img/american_coach/2019 american eagle.png'
import american_eagle_45A from '../img/american_coach/american eagle 45A.png'
import american_eagle_45C from '../img/american_coach/american eagle 45C.png'
import american_eagle_45T from '../img/american_coach/american eagle 45T.png'
import american_eagle_heritage_edition_2019 from '../img/american_coach/2019 american eagle heritage edition.png'
import forceHD from '../img/dynamax/Force HD.png'
import dx3 from '../img/dynamax/DX3.png'
import dynaQuestXL from '../img/dynamax/dynaquest XL.png'
import entegra_insignia_2019 from '../img/entegra/2019 Entegra Insignia.png'
import entegra_cornerstone_2019 from '../img/entegra/2019 Entegra Cornerstone.png'
import entegra_aspire_2019 from '../img/entegra/2019 Entegra Aspire.png'
import entegra_anthem_2019 from '../img/entegra/2019 Entegra Anthem.png'
import entegra_vision_2019 from '../img/entegra/2019 Entegra Vision.png'
import entegra_emblem_2019 from '../img/entegra/2019 Entegra Emblem.png'
import fleetwood_pace_arrow_2019 from '../img/fleetwood/2019 Fleetwood Pace Arrow.png'
import fleetwood_discovery_2019 from '../img/fleetwood/2019 Fleetwood Discovery.png'
import fleetwood_discovery_lxe_2019 from '../img/fleetwood/2019 Fleetwood Discovery LXE.png'
import fleetwood_pace_arrow_lxe_2019 from '../img/fleetwood/2019 Fleetwood Pace Arrow LXE.png'
import fleetwood_bounder_2019 from '../img/fleetwood/2019 Fleetwood Bounder.png'
import fleetwood_southwind_2019 from '../img/fleetwood/2019 Fleetwood Southwind.png'
import monaco_signature_2019 from '../img/monaco/2019 Monaco Signature.png'
import monaco_marquis_2019 from '../img/monaco/2019 Monaco Marquis.png'
import newmar_dutch_star_2019 from '../img/newmar/2019 Newmar Dutch Star.png'
import newmar_essex_2019 from '../img/newmar/2019 Newmar Essex.png'
import newmar_king_aire_2019 from '../img/newmar/2019 Newmar King Aire.png'
import newmar_london_aire_2019 from '../img/newmar/2019 Newmar London Aire.png'
import newmar_mountain_aire_2019 from '../img/newmar/2019 Newmar Mountain Aire.png'
import newmar_new_aire_2019 from '../img/newmar/2019 Newmar New Aire.png'
import newmar_ventana_2019 from '../img/newmar/2019 Newmar Ventana.png'
import newmar_ventana_le_2019 from '../img/newmar/2019 Newmar Ventana LE.png'
import newmar_bay_star_2019 from '../img/newmar/2019 Newmar Bay Star.png'
import newmar_canyon_star_2019 from '../img/newmar/2019 Newmar Canyon Star.png'
import tiffin_allegro_breeze_2019 from '../img/tiffin/2019 Tiffin Allegro Breeze.png'
import tiffin_allegro_bus_2019 from '../img/tiffin/2019 Tiffin Allegro Bus.png'
import tiffin_allegro_red_2019 from '../img/tiffin/2019 Tiffin Allegro Red.png'
import tiffin_open_road_allegro_2019 from '../img/tiffin/2019 Tiffin Open Road Allegro.png'
import tiffin_phaeton_2019 from '../img/tiffin/2019 Tiffin Phaeton.png'
import tiffin_zephyr_2019 from '../img/tiffin/2019 Tiffin Zephyr.png'
import winnebago_forza_2019 from '../img/winnebago/2019 Winnebago Forza.png'
import Winnebago_grand_tour_2019 from '../img/winnebago/2019 Winnebago Grand Tour.png'
import Winnebago_horizon_2019 from '../img/winnebago/2019 Winnebago Horizon.png'
import Winnebago_adventurer_2019 from '../img/winnebago/2019 Winnebago Adventurer.png'
import Winnebago_vista_2019 from '../img/winnebago/2019 Winnebago Vista.png'
import Winnebago_sightseer_2019 from '../img/winnebago/2019 Winnebago Sightseer.png'
import Winnebago_sunova_2019 from '../img/winnebago/2019 Winnebago Sunova.png'


const styles = theme => ({
  root: {
    
  },
  button: {
    marginRight: theme.spacing.unit,
  },
  instructions: {
    marginTop: theme.spacing.unit,
    marginBottom: theme.spacing.unit,
  },
});

class HorizontalLinearStepper extends React.Component {
  state = {
    activeStep: 0,    
    headerImage: logo,
    nextStepDisabled: sessionStorage.getItem("dieselOrGas") === null,
    showLogo: true,
    skipped: new Set(),
  }

  isNewOrPreowned = step => {
    return step === 0
  }

  isStepOptional = step => {
    return false
  }

  handleCard = () => {
    this.props.history.push('/card/' + 
      sessionStorage.getItem("dieselOrGas") + "/" + 
      sessionStorage.getItem("manufacturer").replace(/\s/g,'') + "/" + 
      sessionStorage.getItem('year') + "/" +
      sessionStorage.getItem('model').replace(/\s/g,'') + "/" +
      sessionStorage.getItem('floorplan'))
  }

  handleNext = () => {
    const { activeStep } = this.state
    let { skipped } = this.state
    if (activeStep === 1 && sessionStorage.getItem("manufacturer") === "Other") {
      this.props.history.push('/other/');
    } 
    else if (this.isStepSkipped(activeStep)) {
      skipped = new Set(skipped.values())
      skipped.delete(activeStep)
    }
    else {
      this.setState({
        activeStep: activeStep + 1,
        skipped,
      })
      this.setNextStepDisabled(activeStep + 1)
    }    
  }  

  handleBack = () => {
    this.setState(state => ({
      activeStep: state.activeStep - 1,
      nextStepDisabled: false
    }));    
  };

  handleEnableNextStep = () => {    
    this.setState(state => ({
      nextStepDisabled: false
    }));    
  }

  handleSkip = () => {
    const { activeStep } = this.state;
    if (!this.isStepOptional(activeStep)) {
      // You probably want to guard against something like this,
      // it should never occur unless someone's actively trying to break something.
      throw new Error("You can't skip a step that isn't optional.");
    }

    this.setState(state => {
      const skipped = new Set(state.skipped.values());
      skipped.add(activeStep);
      return {
        activeStep: state.activeStep + 1,
        skipped,
      };
    });
  };

  handleReset = () => {
    this.setState({
      activeStep: 0,
    });
  };


  isStepSkipped(step) {
    return this.state.skipped.has(step);
  }

  setNextStepDisabled(step) {
    switch (step) {
      case 0:        
        if (!this.state.nextStepDisabled && sessionStorage.getItem("dieselOrGas") === null) {         
          this.setState({nextStepDisabled: true})
        }
        else if (this.state.nextStepDisabled && sessionStorage.getItem("dieselOrGas") !== null) {
          this.setState({nextStepDisabled: false})
        }
        break;
      case 1:      
        if (!this.state.nextStepDisabled && sessionStorage.getItem("manufacturer") === null) {
           this.setState({nextStepDisabled: true})
        }
        else if (this.state.nextStepDisabled && sessionStorage.getItem("manufacturer") !== null) {
          this.setState({nextStepDisabled: false})             
        }
        break
      case 2:        
        if (!this.state.nextStepDisabled && sessionStorage.getItem("model") === null) {
          this.setState({nextStepDisabled: true})
        }
        else if (this.state.nextStepDisabled && sessionStorage.getItem("model") !== null) {
          this.setState({nextStepDisabled: false})
        }
        break     
      case 3:
        if (!this.state.nextStepDisabled && sessionStorage.getItem("floorplan") === null) {
          this.setState({nextStepDisabled: true})
        }
        else if (this.state.nextStepDisabled && sessionStorage.getItem("floorplan") !== null) {
          this.setState({nextStepDisabled: false})
        }
      break;   
      default: return false
    }
  }
  getSteps = () => {
    return ['Select Diesel or Gas', 'Which Manufacturer', 'Select your Model', 'Select Floorplan'];
  }


  getStepContent = (step) => {
    switch (step) {
      case 0:
        return <DieselOrGas handlerToUpdate = {this.handleEnableNextStep} />;
      case 1:
        return <Manufacturer handlerToUpdate = {this.handleManufacturerHeader} />;
      case 2:
        return <Model handlerToUpdate = {this.handleModelHeader} />;
      case 3:
        return <Floorplan handlerToUpdate = {this.handleFloorplanHeader} />
      default:
        return ''
    }
  }

  handleManufacturerHeader = () => {
    this.setState(state => ({
      nextStepDisabled: false
    }));
    switch (sessionStorage.getItem('manufacturer')) {
      case 'American Coach':        
        this.setState({headerImage: american_coach_logo})      
      break
      case 'Dynamax':
        this.setState({headerImage: dynamax_logo})
      break
      case 'Entegra':
        this.setState({headerImage: entegra_logo})  
      break
      case 'Fleetwood':
        this.setState({headerImage: fleetwood_logo})
      break
      case 'Monaco':
        this.setState({headerImage: monaco_logo})
      break
      case 'Newmar':
        this.setState({headerImage: newmar_logo})
      break
      case 'Tiffin':
        this.setState({headerImage: tiffin_logo})
      break
      case 'Winnebago':
        this.setState({headerImage: winnebago_logo})
      break
      case 'Other':
        this.setState({headerImage: logo})
      break
    }    
  } 
  
  handleModelHeader = (year_model) => {
    
    this.setState(state => ({
      nextStepDisabled: false
    }));        
    switch (year_model) {
      case '2019 American Dream':
        this.setState({headerImage: american_dream_2019})
      break
      case '2019 American Eagle':
        this.setState({headerImage: american_eagle_2019})
      break
      case '2019 American Eagle Heritage Edition':
        this.setState({headerImage: american_eagle_heritage_edition_2019})
      break
      case '2019 Force HD':
        this.setState({headerImage: forceHD})
      break
      case '2019 DX3':
        this.setState({headerImage: dx3})
      break
      case '2019 DynaQuest XL':
        this.setState({headerImage: dynaQuestXL})
      break
      case '2019 Insignia':
        this.setState({headerImage: entegra_insignia_2019})
      break
      case '2019 Cornerstone':
        this.setState({headerImage: entegra_cornerstone_2019})
      break
      case '2019 Aspire':
        this.setState({headerImage: entegra_aspire_2019})
      break
      case '2019 Anthem':
        this.setState({headerImage: entegra_anthem_2019})
      break    
      case "2019 Vision":
        this.setState({headerImage: entegra_vision_2019})
      break
      case "2019 Emblem":
        this.setState({headerImage: entegra_emblem_2019})
      break
      case '2019 Pace Arrow':
        this.setState({headerImage: fleetwood_pace_arrow_2019})
      break  
      case '2019 Pace Arrow LXE':
        this.setState({headerImage: fleetwood_pace_arrow_lxe_2019})
      break
      case '2019 Bounder':
        this.setState({headerImage: fleetwood_bounder_2019})
      break
      case '2019 Discovery':
        this.setState({headerImage: fleetwood_discovery_2019})
      break
      case '2019 Discovery LXE':
        this.setState({headerImage: fleetwood_discovery_lxe_2019})
      break
      case '2019 Southwind':
        this.setState({headerImage: fleetwood_southwind_2019})
      break
      case '2019 Monaco Signature':
        this.setState({headerImage: monaco_signature_2019})
      break
      case '2019 Monaco Marquis':
        this.setState({headerImage: monaco_marquis_2019})
      break
      case '2019 Dutch Star':
        this.setState({headerImage: newmar_dutch_star_2019})
      break
      case '2019 Essex':
        this.setState({headerImage: newmar_essex_2019})
      break
      case '2019 King Aire':
        this.setState({headerImage: newmar_king_aire_2019})
      break
      case '2019 London Aire':
        this.setState({headerImage: newmar_london_aire_2019})
      break
      case '2019 Mountain Aire':
        this.setState({headerImage: newmar_mountain_aire_2019})
      break
      case '2019 New Aire':
        this.setState({headerImage: newmar_new_aire_2019})
      break      
      case '2019 Ventana':
        this.setState({headerImage: newmar_ventana_2019})
      break
      case '2019 Ventana LE':
        this.setState({headerImage: newmar_ventana_le_2019})
      break
      case '2019 Bay Star':
        this.setState({headerImage: newmar_bay_star_2019})
      break
      case '2019 Canyon Star':
        this.setState({headerImage: newmar_canyon_star_2019})
      break
      case '2019 Allegro Breeze':
        this.setState({headerImage: tiffin_allegro_breeze_2019})
      break
      case '2019 Allegro Bus':
        this.setState({headerImage: tiffin_allegro_bus_2019})
      break
      case '2019 Allegro Red':
        this.setState({headerImage: tiffin_allegro_red_2019})
      break
      case '2019 Open Road Allegro':
        this.setState({headerImage: tiffin_open_road_allegro_2019})
      break
      case '2019 Phaeton':
        this.setState({headerImage: tiffin_phaeton_2019})
      break
      case '2019 Zephyr':
        this.setState({headerImage: tiffin_zephyr_2019})
      break
      case '2019 Forza':
        this.setState({headerImage: winnebago_forza_2019})
      break
      case "2019 Grand Tour":
        this.setState({headerImage: Winnebago_grand_tour_2019})
      break
      case "2019 Horizon":
        this.setState({headerImage: Winnebago_horizon_2019})
      break
      case "2019 Adventurer":
        this.setState({headerImage: Winnebago_adventurer_2019})
      break
      case "2019 Vista":
        this.setState({headerImage: Winnebago_vista_2019})
      break
      case "2019 Sightseer":
        this.setState({headerImage: Winnebago_sightseer_2019})
      break
      case "2019 Sunova":
        this.setState({headerImage: Winnebago_sunova_2019})
      break
      
    }    
  }
 
  handleFloorplanHeader = (floorplan) => {
    this.setState(state => ({
      nextStepDisabled: false
    }));
    switch (floorplan) {
      case "American Eagle 45A":
        this.setState({headerImage: american_eagle_45A})
      break
      case "American Eagle 45C":
        this.setState({headerImage: american_eagle_45C})
      break
      case "American Eagle 45T":
        this.setState({headerImage: american_eagle_45T})
      break
      case "American Dream 42B":
        this.setState({headerImage: american_dream_42B})
      break
      case "American Dream 42Q":
        this.setState({headerImage: american_dream_42Q})
      break
      case "American Dream 42S":
        this.setState({headerImage: american_dream_42S})
      break
      case "American Dream 45A":
        this.setState({headerImage: american_dream_45A})
      break     
      case "American Eagle Heritage Edition 45A":
        this.setState({headerImage: american_eagle_45A})
      break 
      case "American Eagle Heritage Edition 45C":
        this.setState({headerImage: american_eagle_45T})
      break
    }
  }

  render() {
    const { classes } = this.props;
    const steps = this.getSteps();
    const { activeStep } = this.state;       
    return (
      <div className={classes.root}> 
        <header className="App-header"> 
          <img src={this.state.headerImage} className="App-headerImage"  />                                    
          <img src={logo} className='App-logo-hide' alt="logo"   />
        </header>       
        <Stepper activeStep={activeStep} >
          {steps.map((label, index) => {
            const props = {};
            const labelProps = {};
            if (this.isStepOptional(index)) {
              labelProps.optional = <Typography variant="caption">Optional</Typography>;
            }
            if (this.isStepSkipped(index)) {
              props.completed = false;
            }            
            return (
              <Step key={label} {...props}>
                <StepLabel {...labelProps}>{label}</StepLabel>                
              </Step>
            );
          })}
        </Stepper>
        <div>
          {activeStep === steps.length ? (
            <div>
              <Typography component={'span'}  className={classes.instructions}>
                All steps completed - you&apos;re finished
              </Typography>
              <Button onClick={this.handleReset} className={classes.button}>
                Reset
              </Button>
            </div>
          ) : (
            <div>
              <Typography component={'span'}  className={classes.instructions}>{this.getStepContent(activeStep)}</Typography>
              <div>              
                  <Button
                  disabled={activeStep === 0}
                  onClick={this.handleBack}
                  className={classes.button}
                >
                  Back
                </Button>
               
                {this.isStepOptional(activeStep) && (
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={this.handleSkip}
                    className={classes.button}
                  >
                    Skip
                  </Button>
                )}
                <Button
                  variant="contained"
                  color="primary"
                  onClick={activeStep === steps.length - 1 ? this.handleCard : this.handleNext}
                  className={classes.button}
                  disabled={this.state.nextStepDisabled}
                >
                  Next
                </Button>
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }
}

HorizontalLinearStepper.propTypes = {
  classes: PropTypes.object,
};

export default withRouter(withStyles(styles)(HorizontalLinearStepper));